.toggle-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	height: 24px;
	width: 30px;
	border: none;
	background: transparent;
	cursor: pointer;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.toggle-button:focus {
	outline: none;
}

.toggle-button-line {
	width: 30px;
	height: 2px;
	background-color: white;
}


