.side-drawer {
	margin-top: 56px;
	height: 20%;
	background: rgb(15, 77, 211);
	-webkit-box-shadow: 1px 0px 5px rbga(0, 0, 0, 0.2);
	        box-shadow: 1px 0px 5px rbga(0, 0, 0, 0.2);
	position: fixed;
	top: 0;
	left: 0;
	width:  100%;
	max-width: 1920px;
	z-index: 200;
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	-webkit-transition: 0.2s ease-out;
	-o-transition: 0.2s ease-out;
	transition: 0.2s ease-out;
}

.side-drawer.open {
	-webkit-transform: translateY(-40%);
	    -ms-transform: translateY(-40%);
	        transform: translateY(-40%);
}

.side-drawer ul {
	height: 100%;
	list-style: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.side-drawer li {
	margin: 0.5rem 0;
}

.side-drawer a {
	text-decoration: none;
	color: white;
}

@media (min-width: 769px) {
	.side-drawer {
		display: none;
	}
}


